import React, { useState, useEffect } from 'react'
import ArticleList from "../components/ArticleList"
import articleContent from '../article-content'
import NotFoundPage from './NotFoundPage'
import CommentList from '../components/CommentList';

import UpvoteSection from '../components/UpvoteSection';

import AddCommentForm from '../components/AddCommentForm';
// import articles from '../article-content';

// const ArticlePage = () => (

//         <p> articles here!</p>

// );
const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    useEffect(() => {
        const fetchDta = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            if (body !== null)
                setArticleInfo({ upvotes: body.upvotes, comments: body.comments });
            else
                setArticleInfo({ upvotes: 0, comments: [] });

        }

        fetchDta();
        // setArticleInfo({ upvotes: Math.ceil(Math.random() * 10)});
    }, [name]);
    if (!article) {
        return <NotFoundPage />
    }
    // console.log(name)
    const otherArticles = articleContent.filter(article => article.name !== name);


    // return (
    //     <>
    //         <h1>Not Found!</h1>
    //         {/* <p>{1 + 1}</p> */}
    //     </>            
    // )

    return (
        <>
            <h1>{article.title}</h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {/* <p>This post has been upvoted {articleInfo.upvotes} times!</p> */}
            {article.content.map((par, k) => (
                <p key={k}> {par} </p>
            ))}

            <h3>Comments:</h3>
            {articleInfo.comments && articleInfo.comments.length !== 0 ? <CommentList comments={articleInfo.comments} />
                : <p>No comments yet!</p>}
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles</h3>
            <ArticleList articles={otherArticles} />
        </>
    );

}

export default ArticlePage;